import React, { Component } from 'react';
import './Exercise.css';

import { Consumer } from "./AbacusContext";



class Exercise extends Component {
    
    
    render() {
        return (
            <Consumer>
                {context => {
                    return (
                        <>
                            <div style={context.ansPosition === 0 ? {color: "red"} : {color: "black"}}>{context.seq[0]}</div>
                            <div style={context.ansPosition === 1 ? {color: "red"} : {color: "black"}}>{context.seq[1]}</div>
                            <div style={context.ansPosition === 2 ? {color: "red"} : {color: "black"}}>{context.seq[2]}</div>
                            <div className="spacer"></div>
                            <div style={{color: "red"}}>{context.ansPosition === 3 ? context.ans : "\u00a0"}</div>
                        </>
                    )
                }}
            </Consumer>
        )
    }

}



export default Exercise