import React, { Component } from 'react';
import './App.css';
import Abacus from './Abacus'
import Exercise from './Exercise'
import { Consumer } from "./AbacusContext";



class App extends Component {



    render() {
        return (
            <Consumer>
                {context => {

                    if (context.gameStarted === false) {
                        return (
                            <div className="app">
                                <button onMouseUp={(e) => context.actions.startGame(e)}>!</button>
                            </div>
                        )
                    } else {
                        return (
                            <div className="app">

                                <div className="header"></div>
                                <div className="helper"></div>
                                <div className="exercise">
                                    <Exercise />
                                </div>
                                <div className="abacusholder">
                                    <Abacus />
                                </div>
                            </div>
                        )
                    }


                }}

            </Consumer>
        )
    }
}


export default App