import React, { Component } from 'react';
import './InfoMessage.css'
import { Consumer } from "./AbacusContext";


class InfoMessage extends Component {

    render() {
        return (
            <Consumer>
                {context => { 
                    if (context.levelState === "finished") {
                        return <p className="value">  </p>
                    } else 
                        return <p className="value">{context.value}</p>
                }}
            </Consumer>
        )
    }

}

export default InfoMessage
