import React, { Component } from 'react';

import utils from './libs/generator'


function process(cfg) {


    let res = utils.generate(cfg);
    //console.log('Level:', cfg.Level, '| Operation:', cfg.Operation, '| Result:', res);
    //let seq_sum = res.seq.reduce(function (a, b) { return a + b; });
    //let ans = res.answer.reverse().reduce(function (a, b) { return a * 10 + b });
    //console.assert(seq_sum == ans, seq_sum, ans);
    //console.assert(res.seq.length == cfg.Kolslog);
    return res
}






export const MyContext = React.createContext();

export class Provider extends Component {
    state = {
        gameStarted: false,
        levelState: "notstarted",
        seq: [],
        ans: null,
        ansPosition: 0,
        beadHeight: 0,
        value: 0,
        dragged: {
            c: null,
            r: null
        },
        active: false,
        abacusState: [
            [{
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }],
            [{
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }, {
                beadState: 0,
                active: false,
                currentY: 0,
                initialY: 0,
                yOffset: 0,
            }],
        ]
    }

    dragStart = (e) => {

        try {

            if (e.target.id === "sp0") {
                this.resetAbacus();
            }

            let newState = Object.assign({}, this.state);

            newState.dragged.c = parseInt(e.target.id[0]);
            newState.dragged.r = parseInt(e.target.id[2]);
            newState.levelState = "started";
            let c = parseInt(e.target.id[0]);
            let r = parseInt(e.target.id[2]);

            let uch;
            uch = document.querySelector('#uc1').getBoundingClientRect().height;
            newState.beadHeight = uch;  //         ComponentDidMount

            if (e.type === "touchstart") {
                newState.abacusState[c][r].initialY = e.touches[0].clientY - this.state.abacusState[c][r].yOffset;
            } else {
                newState.abacusState[c][r].initialY = e.clientY - this.state.abacusState[c][r].yOffset;
            }

            if (e.target !== e.currentTarget) {
                // newState.abacusState[c][r].active = true;
                newState.active = true;
            }

            this.setState(newState);
        } catch (e) {

        }
    }

    drag = (e) => {
        let c, r;

        try {

            //   if (this.state.abacusState[c][r].active === true) {
            if (this.state.active === true) {

                c = this.state.dragged.c;
                r = this.state.dragged.r;
                let newState = Object.assign({}, this.state);
                let uch = newState.beadHeight;


                // let dragItem = e.target;
                e.preventDefault();

                let currentY;


                if (e.type === "touchmove") {
                    currentY = e.touches[0].clientY - this.state.abacusState[c][r].initialY;
                } else {
                    currentY = e.clientY - this.state.abacusState[c][r].initialY;
                }

                if (!this.state.abacusState[c][r].beadState) {
                    if (r === 0) {
                        if (currentY >= 0) {
                            if (currentY < uch / 4) {
                                newState.abacusState[c][r].currentY = currentY
                                //this.setTranslate(newState.abacusState[c][r].currentY, dragItem);
                            } else {
                                currentY = uch / 2
                                //this.setTranslate(newState.abacusState[c][r].currentY, dragItem);

                                newState.abacusState[c][r].yOffset = currentY;
                                newState.abacusState[c][r].currentY = currentY;
                                newState.abacusState[c][r].beadState = 1;
                                // newState.abacusState[c][r].active = false;
                                newState.active = false;
                                this.setState(newState);
                                return false;
                            }
                        }
                    }

                    if ([1, 2, 3, 4].includes(r)) {
                        if (currentY < 0) {
                            if (currentY > -1 * uch / 4) {
                                // this.setTranslate(currentY, dragItem);
                                newState.abacusState[c][r].currentY = currentY;
                                let firstOne = 1;
                                for (let ind = 1; ind <= 4; ind++) {
                                    if (newState.abacusState[c][ind].beadState === 1) {
                                        firstOne = ind + 1;
                                    }
                                }

                                for (let ind = firstOne; ind <= r; ind++) {
                                    // let item = document.querySelector("#bead_" + c + "_" + ind);
                                    // this.setTranslate(newState.abacusState[c][r].currentY, item);
                                    newState.abacusState[c][ind].currentY = currentY;
                                    newState.abacusState[c][ind].yOffset = currentY;
                                }
                            } else {
                                currentY = -1 * uch / 2;

                                for (let ind = 1; ind <= r; ind++) {
                                    // let item = document.querySelector("#bead_" + c + "_" + ind);
                                    //this.setTranslate(newState.abacusState[c][r].currentY, item);

                                    newState.abacusState[c][ind].beadState = 1;
                                    newState.abacusState[c][ind].yOffset = currentY;
                                    newState.abacusState[c][ind].currentY = currentY;
                                }

                                // newState.abacusState[c][r].active = false;
                                newState.active = false;
                                this.setState(newState);
                                return false;
                            }
                        }
                    }
                }

                if (newState.abacusState[c][r].beadState) {

                    if (r === 0) {

                        if (newState.abacusState[c][r].currentY > uch / 4) {
                            // this.setTranslate(newState.abacusState[c][r].currentY, dragItem);
                            newState.abacusState[c][r].currentY = currentY;
                            newState.abacusState[c][r].yOffset = currentY;
                        }
                        else if (currentY <= uch / 4) {

                            currentY = 0;
                            // this.setTranslate(newState.abacusState[c][r].currentY, dragItem);
                            newState.abacusState[c][r].currentY = currentY;
                            newState.abacusState[c][r].yOffset = currentY;
                            newState.abacusState[c][r].beadState = 0;
                            // newState.abacusState[c][r].active = false;
                            newState.active = false;
                            this.setState(newState);
                            return false;
                        }


                    }

                    if ([1, 2, 3, 4].includes(r)) {
                        if (currentY < 0) {
                            if (currentY <= -1 * uch / 4 && currentY > -1 * uch / 2) {
                                let firstZero = 4;

                                for (let ind = 4; ind > 0; ind--) {
                                    if (newState.abacusState[c][ind].beadState === 0) {
                                        firstZero = ind - 1;
                                    }
                                }
                                for (let ind = r; ind <= firstZero; ind++) {
                                    newState.abacusState[c][ind].currentY = currentY;
                                    newState.abacusState[c][ind].yOffset = currentY;
                                    // let item = document.querySelector("#bead_" + c + "_" + ind);
                                    // this.setTranslate(newState.abacusState[c][r].currentY, item);
                                }
                                //this.setTranslate(currentY, dragItem);

                            }
                            if (currentY > -1 * uch / 4) {
                                currentY = 0;
                                //this.setTranslate(currentY, dragItem);
                                for (let ind = r; ind <= 4; ind++) {
                                    // let item = document.querySelector("#bead_" + c + "_" + ind);
                                    // this.setTranslate(newState.abacusState[c][r].currentY, item);
                                    newState.abacusState[c][ind].beadState = 0;
                                    newState.abacusState[c][ind].yOffset = currentY;
                                    newState.abacusState[c][ind].currentY = currentY;
                                }
                                // newState.abacusState[c][r].active = false;
                                newState.active = false;
                                this.setState(newState);
                                return false;
                            }

                        }


                    }

                }

                this.setState(newState);
            }
        } catch (e) {
        }



    }

    dragEnd = (e) => {


        try {
            let c = this.state.dragged.c;
            let r = this.state.dragged.r;
            let newState = Object.assign({}, this.state);
            let uch = newState.beadHeight;
            let currentY = newState.abacusState[c][r].currentY;



            //   if (newState.abacusState[c][r].active) {
            if (!newState.abacusState[c][r].beadState) {

                if (r === 0) {
                    if (currentY < uch / 2) {
                        currentY = 0;
                        newState.abacusState[c][r].yOffset = currentY;
                        newState.abacusState[c][r].currentY = currentY;
                        // setTranslate(newState.abacusState[c][r].currentY, dragItem);
                    }
                }
                if ([1, 2, 3, 4].includes(r)) {
                    if (currentY > -1 * uch / 4) {
                        let firstOne = 1;
                        for (let ind = 1; ind <= 4; ind++) {
                            if (newState.abacusState[c][ind].beadState === 1) {
                                firstOne = ind + 1;
                            }
                        }
                        for (let ind = firstOne; ind <= r; ind++) {
                            newState.abacusState[c][ind].currentY = 0;
                            newState.abacusState[c][ind].yOffset = 0;
                            // let item = document.querySelector("#bead_" + column + "_" + ind);
                            // setTranslate(abacusState[column][ind].currentY, item);
                        }

                    }
                }
            }

            if (newState.abacusState[c][r].beadState) {
                if (r === 0) {
                    if (currentY > uch / 4) {
                        currentY = uch / 2;
                        newState.abacusState[c][r].currentY = currentY;
                        newState.abacusState[c][r].yOffset = currentY;
                        //   newState.abacusState[c][r].yOffset = newState.abacusState[c][r].yOffset;
                        // setTranslate(newState.abacusState[c][r].currentY, dragItem);
                    }
                }
                if ([1, 2, 3, 4].includes(r)) {

                    if (newState.abacusState[c][r].currentY > -1 * uch / 2) {
                        let firstZero = 4;

                        for (let ind = 4; ind > 0; ind--) {
                            if (newState.abacusState[c][ind].beadState === 0) {
                                firstZero = ind - 1;
                            }
                        }
                        for (let ind = r; ind <= firstZero; ind++) {
                            currentY = -1 * uch / 2;
                            newState.abacusState[c][ind].yOffset = currentY;
                            newState.abacusState[c][ind].currentY = currentY;
                            // abacusState[column][ind].yOffset = abacusState[column][ind].yOffset;
                            // let item = document.querySelector("#bead_" + column + "_" + ind);
                            // setTranslate(abacusState[column][ind].currentY, item);
                        }

                    }
                }

            }





            //   }
            newState.abacusState[c][r].initialY = currentY;
            // newState.abacusState[c][r].active = false;
            newState.active = false;
            newState.dragged.c = null;
            newState.dragged.r = null;
            newState.value = this.calculateValue(newState.abacusState);
            this.setState(newState);
        } catch (e) {

        }


    }

    calculateValue(abacusState) {
        let value0 = abacusState[0][0].beadState * 5 + Math.max(abacusState[0][1].beadState, abacusState[0][2]
            .beadState * 2, abacusState[0][3].beadState * 3, abacusState[0][4].beadState * 4);
        let value1 = 10 * (abacusState[1][0].beadState * 5 + Math.max(abacusState[1][1].beadState, abacusState[
            1][2]
            .beadState * 2, abacusState[1][3].beadState * 3, abacusState[1][4].beadState * 4));
        return value0 + value1;
    }

    resetAbacus = () => {
        if (this.state.ansPosition === 3) {
            this.nextLevel();
        } else {
            let newState = Object.assign({}, this.state);
            newState.abacusState = this.generateEmptyAbacus();
            newState.ansPosition = 0;
            newState.value = 0;
            newState.levelState = "started";
            this.setState(newState);
        }
    }


    generateEmptyAbacus = () => {
        let c = 2;
        let r = 5;
        let abacus = [];
        for (let j = 1; j <= c; j++) {
            abacus.push([])
            for (let i = 1; i <= r; i++) {
                abacus[j - 1].push({
                    beadState: 0,
                    active: false,
                    currentY: 0,
                    initialY: 0,
                    yOffset: 0,
                })
            }
        }
        return abacus
    }

    resetStart = () => {
        document.getElementById("reset").setAttribute("class", "reset reset_clicked");
        this.resetAbacus();
    }

    resetEnd = () => {
        document.getElementById("reset").setAttribute("class", "reset");
    }

    startGame = () => {
        var level = { level: 2, Checked_cifr: [1, 2, 3, 4] };

        var cfg = {
            Checked_cifr: level.Checked_cifr,
            Level: level.level,
            Operation: 2,
            Kolslog: 3,
            Kolcifr: 1
        }
        let res = process(cfg);
        let newState = Object.assign({}, this.state);
        newState.seq = res.seq;
        newState.ans = res.answer[0];
        newState.gameStarted = true;

        this.setState(newState);
    }

    nextLevel = () => {
        let newState = Object.assign({}, this.state);
        newState.ansPosition = 0;
        newState.levelState = "notstarted";
        newState.value = 0;
        newState.abacusState = this.generateEmptyAbacus();
        var level = { level: 2, Checked_cifr: [1, 2, 3, 4] };
        var cfg = {
            Checked_cifr: level.Checked_cifr,
            Level: level.level,
            Operation: 2,
            Kolslog: 3,
            Kolcifr: 1
        }
        let res = process(cfg);
        newState.seq = res.seq;
        newState.ans = res.answer[0];
        this.setState(newState);
    }

    componentDidUpdate() {
        let intermedateSum = 0;
        for (let i = 0; i <= this.state.ansPosition; i++) {
            intermedateSum += this.state.seq[i];
        }
        if (this.state.value === intermedateSum) {
            let newState = Object.assign({}, this.state);
            newState.ansPosition = this.state.ansPosition + 1;
            if (newState.ansPosition === 3) {

                
                newState.levelState = "finished";
                
    
                //this.nextLevel();
            }

            this.setState(newState);
        }



    }

    render() {

        return (
            <MyContext.Provider
                value={{
                    gameStarted: this.state.gameStarted,
                    levelState: this.state.levelState,
                    seq: this.state.seq,
                    ans: this.state.ans,
                    exerciseAnswer: this.state.exerciseAnswer,
                    ansPosition: this.state.ansPosition,
                    value: this.state.value,
                    dragged: this.state.dragged,
                    active: this.state.active,
                    abacusState: this.state.abacusState,
                    actions: {
                        nextExample: this.nextExample,
                        startGame: this.startGame,
                        nextLevel: this.nextLevel,
                        resetStart: this.resetStart,
                        resetEnd: this.resetEnd,
                        dragStart: this.dragStart,
                        drag: this.drag,
                        dragEnd: this.dragEnd,
                    },
                }}
            >
                {this.props.children}
            </MyContext.Provider>
        );
    }
}

export const Consumer = MyContext.Consumer 